
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'

export default{
components: {
XHeader,
XButton,
Box,
XInput,
Flexbox,
FlexboxItem,
XTextarea,
Group,
Cell,
Toast,
Loading,
Scroller,
XTable
},
data () {
return {
inputmessage: '',  //  输入的文本内容
light: true,  //  输入框不为空时，input下边框变色
clickmore: false, // 点击加号底部显示、隐藏
gropname: '', //  聊天名字
ifme: false,  //  发消息的对象是否是自己
enlargeurl: '', //  头像地址
enlargehides: false,
enlargeShow: false,
enlarge: false,
timer: null,
groupconversine: [], // 对话列表
offset: 0,
imgS: '',
scroll: '',
loadStatus: false, // 加载
underscore: false, // 底线
chatData: {},
imgurl: '',
userId: '',
scrollTop: 0,
allgroups: [],    // 所有群聊信息
testLine: '33',
userCounts: 0,
username: ''

}
},
created () {
},
mounted () {
this.$nextTick(() => {
this.$refs.scrollerEvent.reset({top: 0})
})
this.$vux.loading.show({
text: 'loading'
})
this.getUserInfo()
this.$socket.on('userJoined', (msg) => {
console.log('343434343' + msg.numUsers)
this.testLine = msg.username
this.userCounts = msg.numUsers
})
this.$socket.on('newMessage', (msg) => {
if (!msg) {
return
}
console.log('denglule :' + this.username)
console.log(msg.message)
this.groupconversine.push(msg)
this.$nextTick(() => {



this.$refs.hideId.scrollIntoView()
})





})
},
computed: {},
beforeDestroy () {
},
methods: {
saySom () {

const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
const recognition = new SpeechRecognition()
recognition.lang = 'en-US' 
recognition.interimResults = false
recognition.start()
recognition.addEventListener('result', (e) => {
let last = e.results.length - 1
let text = e.results[last][0].transcript
console.log('Confidence: ' + e.results[0][0].confidence)

this.$socket.emit('voice message', text)
})
},
enterThing () {

},
enlargeHide () {

},
enlargeImg (enlargeImg) {
this.enlargeurl = enlargeImg
this.enlarge = true
this.enlargeShow = true
this.enlargehides = false
},
loadMore () {
},
inputBottomHide () {
this.clickmore = false
},
onScroll (pos) {
this.scrollTop = pos.top
},
onEventFocus () {
setTimeout(() => {
let pannel = document.getElementById('chatpannel')
pannel.scrollIntoView(true)
pannel.scrollIntoViewIfNeeded()
}, 200)
},
whatInput () {
if (this.inputmessage.replace(/\s+/g, '') === '') {
this.light = true
} else {
this.light = false
}
},
bottomShow () {
this.clickmore = true
},
async clickSend () {
console.log('inputMessage:' + this.inputmessage)
this.$socket.emit('newMessage', this.inputmessage)
this.inputmessage = ''
this.light = false
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
this.username = res.data.username
this.$socket.emit('add user', res.data.username)
console.log('denglule1 :' + this.username)
this.$vux.loading.hide()
})
.catch((error) => {
console.log(error)
this.$vux.loading.hide()
})
}
}
}



